/**
 * friction factor
 * @customfunction
 * @param {number} dia diameter, mm
 * @param {number} roughness absolute roughness, mm
 * @param {number} reynolds reynolds number
 * @returns {number} friction factor
 */
export function frictionfactor(dia, roughness, reynolds) {
  // FrictionCalc using Niazkar Method
  let a, b, c, Freverse;
  a = -2 * Math.log10(roughness / dia / 3.7 + 4.5547 / Math.pow(reynolds, 0.8784));
  b = -2 * Math.log10(roughness / dia / 3.7 + (2.51 * a) / reynolds);
  c = -2 * Math.log10(roughness / dia / 3.7 + (2.51 * b) / reynolds);
  Freverse = Math.pow(a - Math.pow(b - a, 2) / (c - 2 * b + a), 2);
  return 1 / Freverse;
}

